.channelRow {
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 70%;
}

.channelRow__text {
  display: flex;
  flex-direction: column;
}

.channelRow__text > p {
  color: #606060;
  font-size: small !important;
}

.channelRow__logo {
  height: 120px !important;
  width: 120px !important;
  margin: 10px !important;
}

.channelRow__text > h4 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 520px) {

  .channelRow{
    width: 100%;
  }
}